














import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

@Component
export default class PositionSwitcherComponent extends Vue {
    private editMode!: string;

    public data(): any {
        return {
            editMode: 'draggable',
        };
    }

    @Watch('editMode')
    private onChangeMode() {
        this.$emit('onChangeMode', this.editMode);
    }
}
